var DegreeSearch;
(function (DegreeSearch) {
    (function (Controllers) {
        var ProgramController = (function () {
            function ProgramController($scope, degreeService) {
                this.scope = $scope;
                this.degreeService = degreeService;
                this.mainCtl = this.scope.$parent.mainCtl;
                this.programTypes = new Array();
                this.setProgramTypes(true);
            }
            ProgramController.prototype.setProgramTypes = function (init) {
                var _this = this;
                if (typeof init === "undefined") { init = false; }
                this.degreeService.getProgramTypes(function (response) {
                    if (init) {
                        _this.addHandlers();
                    }

                    _this.programSuccess(response);
                    _this.registerRoutes();
                }, function (response) {
                    _this.programError(response);
                });
            };

            ProgramController.prototype.addHandlers = function () {
                var _this = this;
                this.scope.$watch('mainCtl.searchQuery', function (query) {
                    _this.onQueryChange(query);
                });
            };

            ProgramController.prototype.registerRoutes = function () {
                var programSlugs = new Array();

                this.programTypes.forEach(function (type) {
                    programSlugs.push(type.slug);
                });

                this.mainCtl.routeRegExps.program = new RegExp('\/(' + programSlugs.join('|') + ')\/?');
            };

            ProgramController.prototype.programSuccess = function (response) {
                this.programTypes = response.data;
            };

            ProgramController.prototype.programError = function (response) {
                this.programTypes = new Array();
            };

            ProgramController.prototype.onSelected = function (value) {
                this.mainCtl.selectedProgramType = value;
                this.mainCtl.page = 1;
                this.mainCtl.getSearchResults();
            };

            ProgramController.prototype.onQueryChange = function (query) {
                var _this = this;
                if (query) {
                    this.degreeService.getProgramTypesCounts(query, function (response) {
                        _this.updateCounts(response);
                    }, function (response) {
                        // Error occurred. Remove count.
                        _this.programTypes.forEach(function (type) {
                            type.count = null;
                        });
                    });
                } else {
                    this.setProgramTypes();
                }
            };

            ProgramController.prototype.updateCounts = function (response) {
                var counts = response.data;

                this.programTypes.forEach(function (type) {
                    if (typeof counts[type.slug] !== 'undefined') {
                        type.count = counts[type.slug];
                    } else {
                        type.count = 0;
                    }
                });
            };
            ProgramController.$inject = ['$scope', 'DegreeService'];
            return ProgramController;
        })();
        Controllers.ProgramController = ProgramController;
    })(DegreeSearch.Controllers || (DegreeSearch.Controllers = {}));
    var Controllers = DegreeSearch.Controllers;
})(DegreeSearch || (DegreeSearch = {}));
